body{
    background-image: url(Backgound.jpg);
    background-size: 100%;
}

button{
    width: 200px;
    height: 50px;
    margin: 1%;
    font-size: xx-large;
    border-radius: 30px;
    background: none;
    color: black;
    text-shadow: 5px;
    font-weight: 500;
    border: 5px solid black;
}

button:hover{
    background-color: orange;
    color: white;
    cursor: pointer;
}

input{
    font-size: xx-large;
    border-radius: 30px;
    background: none;
}

.inputLBL{
    font-size: xx-large;
}

select{
    font-size: xx-large;
    border-radius: 30px;
    background: none;
}

.errLBL{
    color: red;
    font-size: 30px;
    font-weight: 800;
    font-family: Arial, Helvetica, sans-serif;
    letter-spacing: 5px;
}

.wellcomeDiv{
    text-align: center;
}

.signupDiv{
    text-align: center;
}
